<template>
  <div @click="heroClick()">
    <div class="hero" :class="heroClass" ></div>
    <span class="hero-label">{{ hsClass.name }}</span>
  </div>

</template>


<script>

  export default {
    props: ['classKey', 'hsClass'],
    computed: {
      heroClass () {
        return 'hero-' + this.classKey
      }
    },
    methods: {
      heroClick () {
        this.$emit('hero-click', this.classKey)
      }
    }
  }

</script>

